@import 'src/variables';

.selected-item-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  background: $white;
  border-radius: $border-radius;

  .header, .content {
    padding-left: 1.5rem;
    padding-right: 0.75em;
  }

  h4 {
    font-size: 1.125rem;
    margin: 0 0 0.5rem 0;
  }

  .header {
    padding-top: 0.75rem;
    padding-bottom: 0.75rem;
    border-bottom: 1px solid $default-grey-subtle;

    h3 {
      font-size: 1.25rem;
      margin: 0;
    }
  }

  .content {
    position: relative;
    flex-grow: 1;
    display: flex;
    padding-top: 1rem;
    padding-bottom: 1.5rem;

    & > div {
      flex-grow: 1;
    }

    .tabs {
      overflow: hidden;

      .MuiTabs-root {
        display: flex;
        align-items: center;
        min-height: unset;

        .MuiTabs-scroller:last-child {
          margin-left: 2rem;
        }

        .MuiTabScrollButton-root {
          width: auto;
          height: 1.5rem;

          &:first-child {
            padding-right: 0.75rem;
          }
          &:last-child {
            padding-left: 0.75rem;
          }
        }
      }

      .tab-content {
        padding: 0 2rem;
      }
    }

    .informations-wrapper {
      margin-right: 0.75rem;
      flex-grow: 0;
    }

    .tabs-menu {
      margin-bottom: 1.5rem;
    }

    h5 {
      font-size: 1rem;
      margin: 0 0 0.5rem 0;
    }
  }

  ul {
    margin: 0;
    padding-left: 1rem;
  }
}
